@import ../../../sass/index

.notification
  position: absolute
  top: min(100px, 20vw)
  right: -100%
  width: min(300px, 70vw)
  background-color: red
  border-radius: 10px
  transition: all 1s ease
  &__body
    padding: 10px 30px
    display: flex
    flex-direction: row
    justify-content: space-between
    align-items: center
  &__info
    
  &__title
    margin: 0
    padding: 0
  &__time
    margin: 0
    padding: 0
  &__text
    margin: 0
    padding: 0
  &.active
    right: min(20px, 10vw)